.profile-wrapper {
	display: block;
	margin: 0 auto;
	border-radius: 20px;
	background-color: #fafafa;
	max-width: 300px;
	padding: 15px;
	text-align: center;
	background: rgb(250, 250, 250);
	background: linear-gradient(
		180deg,
		rgba(250, 250, 250, 1) 70%,
		rgba(210, 210, 240, 1) 70%,
		rgba(210, 210, 240, 1) 100%
	);
}

.name {
	font-size: 18px;
	font-weight: 500;
}

.tag {
	color: var(--secondary-text-color);
	font-size: 16px;
}

.location {
	color: var(--secondary-text-color);
	font-size: 16px;
}
.stats {
	display: flex;
	align-content: center;
	justify-content: space-evenly;

	margin: 0;
	padding: 15px;
	list-style: none;
}

.item {
	margin: 0;
	padding: 15px;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.item:not(:last-child) {
	border-right: 1px solid var(--secondary-text-color);
}

.label {
	font-size: 14px;
	color: var(--secondary-text-color);
	margin-bottom: 5px;
}

.quantity {
	font-size: 18px;
	font-weight: 500;
}
